html {
  font-size: 10px;
  font-family: 'Exo 2', sans-serif;
}

.wrapper {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}

.upsection {
  background-color: #f5f5f9;
  display: block;
  width: 100%;
}

.headermenu {
  position: relative;
  background-color: #ffffff;
  font-size: 1.4rem;
}

.headermenu #aBack {
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  color: #404041;
}

.headermenu .back {
  width: calc(100vw - 30px);
  padding: 0;
  margin: 0;
}

.headermenu .mainmenu {
  min-height: 100px;
}

.headermenu .mainmenu .mainmenu__top {
  margin: 10px 0;
  display: flex;
  flex-wrap: nowrap;
}

.headermenu .mainmenu .mainmenu__top .burger {
  padding-left: 10px;
  padding-right: 10px;
}

.headermenu .mainmenu .mainmenu__top .burger i.fa {
  font-size: 40px;
  color: black;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__logo img {
  min-height: 40px;
  min-width: 100px;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search {
  width: 100%;
  padding-right: 20px;
  box-sizing: border-box;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .info {
  margin: 0 0 0.625rem -1rem;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .info__item {
  margin: 0 1rem 0.625rem 1rem;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search {
  margin: 0;
  display: flex;
  justify-content: space-between;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search button {
  border: none;
  cursor: pointer;
  text-align: right;
  background-color: transparent;
  top: inherit;
  right: inherit;
  line-height: 2.857;
  margin: 0 0 0 -1rem;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search button i {
  color: #404041;
  font-size: 1rem;
  /*@include xxs(position, absolute);
                            @include xxs(top, 20%);
                            @include xxs(right, 80px);*/
  border: none;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search button:focus {
  border: none;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search__text {
  flex-grow: 1;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search__icon {
  flex-grow: 0;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search input {
  color: #7f7f7f;
  width: 100%;
  height: inherit;
  line-height: 2.857;
  border: 1px solid #e7e7e7;
  border-radius: 2px;
  line-height: 2.857;
  padding: 0 0.625rem;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__search .search input:focus {
  color: #404041;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__basket {
  border-radius: 2px;
  background-color: #efeff1;
  min-width: 5rem;
  height: 5rem;
  text-align: center;
  position: relative;
  align-self: flex-end;
  margin-right: 2rem;
  cursor: pointer;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__basket:hover {
  background-color: #7f7f7f;
}

.headermenu .mainmenu .mainmenu__top .mainmenu__basket img {
  transform: translateY(25%);
}

.headermenu .mainmenu .mainmenu__top .mainmenu__basket .amount {
  border-radius: 50%;
  background-color: #ec4324;
  position: absolute;
  width: 2rem;
  height: 2rem;
  color: white;
  top: -1rem;
  right: -1rem;
}

.headermenu .mainmenu .menu {
  z-index: 100;
  background-color: #fff;
  width: 100%;
}

.headermenu .mainmenu .menu ul {
  display: flex;
  justify-content: space-between;
}

.headermenu .mainmenu .menu ul.topnav1 {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
  width: 40px;
}

.headermenu .mainmenu .menu .menu ul.topnav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
  position: absolute;
}

.headermenu .mainmenu .menu ul.topnav li {
  float: left;
}

.headermenu .mainmenu .menu ul.topnav1 li a i {
  font-size: 3.6rem;
}

.headermenu .mainmenu .menu ul.topnav1 li span {
  font-size: 24px;
  color: #404041;
  padding-right: 10px;
}

.headermenu .mainmenu .menu .arrow {
  display: none;
}

.headermenu .mainmenu .menu .arrow i {
  float: right;
}

.headermenu .mainmenu .menu ul.topnav1 li a {
  text-decoration: none;
  transition: 0.3s;
  font-size: 17px;
  display: inline-block;
  text-align: center;
  padding: 14px 16px;
  color: black;
}

.headermenu .mainmenu .menu ul.topnav li a {
  text-decoration: none;
  transition: 0.3s;
  font-size: 15px;
  color: #404041;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  padding: 14px 16px;
  border-bottom: 2px solid #fff;
}

.headermenu .mainmenu .menu ul.topnav li a:hover {
  background-color: white;
}

.headermenu .mainmenu .menu ul.topnav > li > a:hover {
  background-color: #fff;
  border-bottom: 2px solid red;
}

.headermenu .mainmenu .menu ul.topnav li:nth-child(8) > a:hover {
  border-bottom: 2px solid black;
}

.headermenu .mainmenu .menu ul.topnav li:nth-child(8) a {
  color: red;
}

.headermenu .mainmenu .menu ul.topnav li:nth-child(n+9) a {
  color: white;
  background-color: #404041;
}

.headermenu .mainmenu .menu ul.topnav li.icon {
  display: none;
}

.headermenu .mainmenu .menu ul.topnav li.back {
  display: none;
}

.headermenu .mainmenu .menu .submenuWraper {
  background-color: white;
  position: absolute;
  z-index: 101;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 43vh;
  overflow: hidden;
  width: inherit;
}

.headermenu .mainmenu .menu .submenuWraper div {
  line-height: 2.143;
}

.headermenu .mainmenu .menu .submenuWraper div a {
  font-size: 14px;
  font-weight: bold;
}

.headermenu .mainmenu .menu .submenuWraper div:nth-child(5) {
  height: 100%;
}

.headermenu .mainmenu .menu ul.submenu {
  list-style: none;
  display: block;
  padding: 0;
}

.headermenu .mainmenu .menu ul.submenu > li {
  line-height: 1.875;
  display: block;
  text-transform: none;
  float: none;
  font-size: 16px;
}

.headermenu .mainmenu .menu .dropdown a {
  border-bottom: 1px solid #f5f5f9;
}

.headermenu .mainmenu .menu .dropdown a:hover {
  background-color: #999;
}

.headermenu .mainmenu .menu .dropbtn {
  display: inline-block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.headermenu .mainmenu .menu .dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  /*min-width: 160px;*/
  max-width: 1180px;
  width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: bold;
}

.headermenu .mainmenu .menu .dropdown-content1 {
  display: none;
  position: absolute;
  /*min-width: 160px;*/
  width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.headermenu .mainmenu .menu .dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block !important;
  text-align: left  !important;
}

.headermenu .mainmenu .menu .show {
  display: block;
}

.banner {
  overflow: hidden;
  margin-top: 0px;
  text-align: center;
  position: relative;
  width: 100%;
  height: calc(100vw/1280*490);
}

.banner > input {
  display: none;
}

.banner > input + label {
  width: 1.5625vw;
  height: 6px;
  border-radius: 1px;
  background-color: #bfbfbf;
  position: relative;
  cursor: pointer;
  z-index: 100;
  display: inline-block;
  margin: 0.78125vw;
  left: 40vw;
  top: 90%;
}

.banner > input + label:after {
  content: '';
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #ee5a3f;
  opacity: 0;
  border: none;
}

.banner > input:checked + label {
  background-color: #ee5a3f;
}

.banner > input:checked + label:after {
  opacity: 1;
}

.banner > input + label + label,
.banner > input + label + label + label {
  display: none;
}

.banner > input:checked + label + label,
.banner > input:checked + label + label + label {
  display: block;
  opacity: 0.8;
  transition: opacity 0.8s ease-out 0s;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #ee5a3f;
  font-size: 12vw;
  z-index: 9;
  padding: 0 1.875rem;
}

.banner > input:checked + label + label:hover,
.banner > input:checked + label + label + label:hover {
  opacity: 0.6;
}

.banner input:checked + label + label {
  left: 0px;
}

.banner > input:checked + label + label + label {
  left: 90vw;
}

.banner > div {
  position: absolute;
  top: 0;
  left: 0;
  /*bottom: 0;*/
  z-index: 0;
  width: 500%;
  text-align: left;
  transition: left 500ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
}

.banner > input:nth-of-type(2):checked ~ div {
  left: -100vw;
}

.banner > input:nth-of-type(3):checked ~ div {
  left: -200vw;
}

.banner > input:nth-of-type(4):checked ~ div {
  left: -300vw;
}

.banner > div > div {
  display: inline-block;
  position: relative;
}

.banner > div > div img {
  height: calc(100vw/1280*490);
}

