.modal {
  display: none;
  background-color: #fff;
  width: calc(100vw/12*8);
  color: #404041;
  position: fixed;
  top: 200px;
  left: calc(100vw/12*2);
  z-index: 121;
  font-family: 'Exo 2', sans-serif;
}

.overlay {
  display: none;
  opacity: 0.451;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 120;
  top: 0;
  left: 0;
  background-color: #404041;
}

.open {
  display: block;
}

.for-city-popup,
.for-cityChoice-popup {
  display: none;
}

.closePopup {
  position: absolute;
  z-index: 122;
  top: 10px;
  right: 10px;
  transform: translateX(-50%);
  color: #404041;
}

.city-popup {
  text-align: center;
  width: calc(100vw/12*6);
  padding: 10px 0;
}

.city-popup span {
  font-size: 2.8rem;
  font-weight: bold;
  font-family: 'Exo 2', sans-serif;
}

.city-popup .button {
  display: inline-block;
  color: #404041;
  text-transform: uppercase;
  width: calc(100vw/12*2);
  margin: 3.2rem 0;
}

.city-popup .button:hover {
  color: #fff;
  background-color: #404041;
}

.cityChoice-popup {
  box-sizing: border-box;
  padding: 10px;
  text-align: center;
  width: inherit;
  z-index: 122;
}

.cityChoice-popup .title {
  font-size: 2.8rem;
  font-weight: bold;
  font-family: 'Exo 2', sans-serif;
}

.cityChoice-popup input {
  margin: 30px;
}

.cityChoice-popup div {
  display: flex;
  justify-content: space-between;
}

.cityChoice-popup div ul {
  list-style: none;
  text-align: left;
}

.container {
  padding: 0 5rem;
  background-color: #f5f5f9;
}

.up {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 -1rem;
  text-align: left;
  font-size: 1.4rem;
  color: #404041;
  line-height: 2.857;
  font-size: 1.4rem;
}

.up__city {
  width: calc(100% / 12 * 2 - 2rem);
  margin: 0 1rem;
  color: #3d3d3d;
}

.up__city i {
