}

.up__delivery {
  width: calc(100% / 12 * 6 - 2rem);
  margin: 0 1rem;
}

.up__phone {
  width: calc(100% / 12 * 2 - 2rem);
  margin: 0 1rem;
  font-size: 1.6rem;
  font-family: "Arial";
  font-weight: bold;
  line-height: 2.5;
}

.up__callMe {
  width: calc(100% / 12 * 2 - 2rem);
  margin: 0 1rem;
  color: white;
  background-color: #ee5a3f;
  text-align: center;
}

.up__callMe .border-bottom-dashed {
  border-bottom: 1px dashed white;
}

.up__close {
  display: none;
}

.burgerInfo {
  display: block;
  padding: 0 20px;
}

.burgerInfo .up {
  margin: 0 -10px;
  display: flex;
}

.burgerInfo .up__phone {
  order: 1;
  width: 100%;
}

.burgerInfo .up__city {
  order: 2;
