.hitsell {
  background-color: #ffffff;
  padding: 15px 0;
}

.hitsell h2 {
  margin-top: 60px;
  margin-bottom: 40px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.8rem;
  font-family: "Exo 2";
  text-align: center;
  color: #363636;
}

.hitsell .focusItem {
  visibility: hidden;
  z-index: 10;
  position: absolute;
  width: 100%;
  height: 550px;
  background-color: white;
  box-shadow: 0px 6px 20px 0px rgba(43, 43, 43, 0.15);
}

.hitsell .focusItem__slider .button {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(33.33333% - 10px);
  align-self: center;
}

.hitsell .focusItem__slider .button:hover {
  cursor: pointer;
  background-color: #000000;
}

.hitsell .focusItem__slider .button:hover h2 {
  color: #ffffff;
}

.hitsell .focusItem__slider .button h2 {
  margin: 0px;
  font-weight: normal;
}

.hitsell .focusItem__slider i {
  color: red;
}

.hitsell .focusItem__slider .wrap {
  display: flex;
  align-items: stretch;
  width: 100%;
  margin: auto;
  flex-direction: column;
  position: relative;
}

.hitsell .focusItem__slider .button_prev {
  top: 45%;
  left: 0;
  position: absolute;
  width: 40px;
  border: none;
  background: white;
  font-size: 30px;
  text-align: center;
  outline: none;
  opacity: 0.7;
  transition: opacity 300ms ease-out;
  cursor: pointer;
}

.hitsell .focusItem__slider .button_next {
  top: 45%;
  right: 0%;
  position: absolute;
  width: 40px;
  border: none;
  background: white;
  font-size: 30px;
  text-align: center;
  outline: none;
  opacity: 0.7;
  transition: opacity 300ms ease-out;
  cursor: pointer;
}

.hitsell .focusItem__slider button:hover {
  opacity: 0.5;
  background: #ee5a3f;
}

.hitsell .focusItem__slider button:hover i {
  color: white;
}

.hitsell .focusItem__slider .slider {
  display: flex;
  flex-direction: row;
  /**/
}

.hitsell .focusItem__slider .scroller {
  flex: 1;
  overflow: hidden;
}

.hitsell .focusItem__slider .items {
  list-style-type: none;
  white-space: nowrap;
  transition: transform 1s ease-in-out;
}

.hitsell .focusItem__slider .item {
  display: inline-block;
  width: 100%;
  height: 230px;
  background-size: cover;
  background-position: 50% 50%;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.hitsell .focusItem .focusItem__footer {
  padding-left: 20px;
  padding-right: 20px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.hitsell .focusItem .focusItem__footer img {
  width: 30px;
  height: 30px;
}

.hitsell .focusItem .focusItem__footer .img__active {
  border: 1px solid red;
}

.hitsell .focusItem .focusItem__footer .focusItem__describe h2 {
  font-size: 2.0rem;
  font-family: "Exo 2";
  color: #404041;
  text-transform: uppercase;
  margin: 0px;
  font-weight: bold;
  text-align: left;
}

.hitsell .focusItem .focusItem__footer .focusItem__factory span {
  font-size: 1.6rem;
  color: #7f7f7f;
}

.hitsell .focusItem .focusItem__footer .focusItem__factory .focusItem__price .price {
  font-size: 2.6rem;
